    <div class="m-rank">
      <h2 class="m-title">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>
        <span class="u-text">贡献榜</span>
      </h2>
      <el-tabs v-model="active_rank_type">
        <el-tab-pane
            v-for="(type, key) in rank_types"
            :key="key"
            :label="type.name"
            :name="type.sub"
        >
          <ul class="u-list">
            <li v-for="(rank, k) in ranks" :key="k">
              <a class="u-contributor" :href="rank.user_id ? author_url(rank.user_id) : null">
                <i class="u-avatar">
                  <img :src="rank.user_avatar | resolveAvatarPath" :alt="rank.user_nickname"/>
                </i>
                <span class="u-name" v-text="rank.user_nickname"></span>
                <em class="u-count"
                >+ {{ rank.total_score }}</em
                >
              </a>
            </li>
          </ul>
        </el-tab-pane>
